@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;400;700&display=swap);
.Sidebar {
  width: 78px;
  padding: 24px 15px 0 15px;
  position: fixed;
  right: 0;
  top: 0;
  bottom: 0;
  background: #050018;
  transition: all 0.5s ease;
}

.Sidebar .links-name {
  opacity: 0;
  pointer-events: none;
}

.Sidebar--active .links-name {
  opacity: 1;
  pointer-events: auto;
}

.Sidebar--active {
  width: 240px;
}

.Sidebar--active .Sidebar__content a {
  display: flex;
}

.Sidebar__content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.Sidebar__content a {
  display: none;
  margin-left: 15px;
  line-height: 28px;
}

.Sidebar-menu {
  margin-left: 10px;
  font-size: 2.2rem;
}

.Sidebar__list {
  margin: 0;
  padding: 24px 0 0 0;
}

.Sidebar__item {
  height: 50px;
  position: relative;
  text-align: center;
}

.Sidebar__item a {
  display: flex;
  color: #ffffff;
  align-items: center;
  line-height: 40px;
  border-radius: 8px;
  transition: all 0.4s ease;
  white-space: nowrap;
}

.Sidebar__item-tooltip {
  position: absolute;
  height: 35px;
  line-height: 35px;
  width: 122px;
  top: 0;
  left: -112px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  background: #ffffff;
  border-radius: 6px;
  box-shadow: 0 5px 10px #0000003d;
  color: #050018;
  transition: 0s;
  opacity: 0;
  pointer-events: none;
}

.Sidebar__item a:hover {
  color: #050018;
  background: #ffffff;
}

.Sidebar__item a i {
  min-width: 50px;
}

.Sidebar__item:hover .Sidebar__item-tooltip {
  transition: all 0.5s ease;
  opacity: 1;
  top: 50%;
}

.Sidebar--active .Sidebar__item-tooltip {
  display: none;
}

.Navbar {
  height: 70px;
  width: 100%;
  position: absolute;
  top: 0;
  padding: 0 5%;
  color: #ffffff;
}

.Navbar__content {
  height: inherit;
  display: flex;
  align-items: center;
}

.Brand-logo {
  display: flex;
  align-items: center;
  font-size: 2rem;
  font-weight: 700;
}

.Brand-logo i {
  font-size: 2.2rem;
  font-weight: 700;
  padding-right: 2px;
}

.Footer {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  text-align: center;
  padding: 10px 0;
}

.Footer span {
  display: block;
  font-size: 1.4rem;
  color: #b4b4b4;
}

.Footer span i {
  color: #9800c7;
}

.Landing {
  height: 100vh;
  display: flex;
  align-items: center;
  background-image: linear-gradient(90deg, #00010e 25%, #00010c2c, #00010c 95%),
    url(/static/media/background.236736ea.png);
  background-position: center;
  background-size: auto;
  background-repeat: no-repeat;
  color: #ffffff;
}

.Landing-info {
  margin: 0px 80px 0px 5%;
}

.title {
  font-size: 5rem;
  color: #6900a7;
  margin: 0;
}

.subtitle {
  font-size: 3rem;
  margin-top: 0px;
}

.Landing-info a {
  font-size: 1.4rem;
}

.subtitle--imp {
  color: #6900a7;
}

@media screen and (min-width: 600px) {
  .title {
    font-size: 5.5rem;
  }

  .subtitle {
    font-size: 3.5rem;
  }

  .Landing-info a {
    font-size: 1.7rem;
  }
}

@media screen and (min-width: 1200px) {
  .Landing {
    background-position: right;
  }
}

.info {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.info h1 {
  margin-top: 0px;
  color: white;
}

.info img {
  width: 500px;
  margin: 0 auto;
}

* {
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  font-size: 1.7rem;
  background: linear-gradient(90deg, #00010e, #00010e);
}

ul,
a {
  list-style: none;
  text-decoration: none;
  color: inherit;
}

.btn {
  cursor: pointer;
  background: transparent;
  border: none;
  color: inherit;
}

.btn--link {
  padding: 10px 15px;
  border: 1px solid #6900a7;
  border-radius: 10px;
  transition: background 0.5s ease, color 0.5s ease;
}

.btn--link:hover {
  background: #6900a7;
  color: #050018;
}

